.back-top {
	position:fixed;
	bottom: rem-calc(5);
	right: rem-calc(5);
	@include breakpoint(large) {
    bottom: rem-calc(20);
		right: rem-calc(20); 
  }
	z-index: 50;
	&__btn {
		display: inline-block;
    background-color: $elements-bg;
    overflow: hidden;
    width: rem-calc(45);
    height: rem-calc(45);
    border: none;
    color: $white;
	  font-size: rem-calc(42);
	  line-height: rem-calc(42);
	  text-align: center; 
    &:hover {
    	background-color: $elements-hov;
    }
	}
}